html {
  scroll-behavior: smooth;
}

.inline-photo {
  border: 1em solid #fff;
  border-bottom: 4em solid #fff;
  border-radius: 0.25em;
  box-shadow: 1em 1em 2em 0.25em rgba(0, 0, 0, 0.2);
  margin: 2em auto;
  transform: rotateZ(-2deg);
  max-width: 600px;
  width: 90%;
}

.photo-block {
  width: 30%;
  text-align: center;
  margin-left: 35%;
  color: #302f2f;
}

.title-block {
  /* height: 100%; */
  /* width: 100px; */
  margin-left: 25%;
  overflow: auto;
}

#section1 {
  height: 100%;
  margin-bottom: 50%;
}

#section2 {
  height: 100%;
  margin-bottom: 20%;
}
